<template>
  <div class="monster-collect">
    <div class="collect-bg" :style="{ backgroundImage: 'url(' + cityBg + ')' }"></div>
    <!-- 抓到三隻怪奇的提示文字 -->
    <div v-if="getThree" class="collect-hint-get-three">
      已抓到3隻怪奇，你可以選擇繼續追捕～或點選緝捕歸案結束工作。
    </div>
    <div class="collect-box">
      <!-- ling -->
      <div class="collect-arrow-box">
        <img v-if="isShowLingArrow && isLingWin" style="margin-right: 22px;" class='collect-arrow' src='@/assets/images/frame-arrow.png' alt='arrived'>
        <img v-if="lingCardType === 'color' && isLingWin" @click="showBigLing" class='collect-ling-icon' src='@/assets/images/ling/ling-color-album.png' alt='arrived'>
        <img v-else-if="lingCardType === 'grey' && isLingWin" @click="showBigLing" class='collect-ling-icon' src='@/assets/images/ling/black-white-ling-icon.png' alt='arrived'>
      </div>
      <!-- lulu -->
      <div class="collect-arrow-box">
        <img v-if="isShowLuluArrow && isLuluWin" class='collect-arrow' src='@/assets/images/frame-arrow.png' alt='arrived'>
        <img v-if="luluCardType === 'color' && isLuluWin" @click="showBigLulu" class='collect-ling-icon' src='@/assets/images/lulu/lulu-full-icon.png' alt='arrived'>
        <img v-else-if="luluCardType === 'grey' && isLuluWin" @click="showBigLulu" class='collect-ling-icon' src='@/assets/images/lulu/lulu-grey-icon.png' alt='arrived'>
      </div>
      <!-- river -->
      <div class="collect-arrow-box">
        <img v-if="isShowRiverArrow && isRiverWin" class='collect-arrow' src='@/assets/images/frame-arrow.png' alt='arrived'>
        <img v-if="riverCardType === 'color' && isRiverWin" @click="showBigRiver" class='collect-ling-icon' src='@/assets/images/river/river-color-icon.png' alt='arrived'>
        <img v-else-if="riverCardType === 'grey' && isRiverWin" @click="showBigRiver" class='collect-ling-icon' src='@/assets/images/river/river-grey-icon.png' alt='arrived'>
      </div>
      <!-- kim -->
      <div class="collect-arrow-box">
        <img v-if="isShowKimArrow && isKimWin" class='collect-arrow' src='@/assets/images/frame-arrow.png' alt='arrived'>
        <img style="width: 18%;" v-if="kimCardType === 'color' && isKimWin" @click="showBigKim" class='collect-ling-icon' src='@/assets/images/commute/passenger_colorful.png' alt='arrived'>
        <img style="width: 18%;" v-else-if="kimCardType === 'grey' && isKimWin" @click="showBigKim" class='collect-ling-icon' src='@/assets/images/commute/kim-gray-capture.png' alt='arrived'>
      </div>
    </div>
    <!-- 結局 -->
    <div v-if="getAll || getThree" class="collect-btn-box">
      <div v-if="!whoWin" @click="openPapper" class="collect-btn">緝捕歸案</div>
      <div v-if="whoWin" v-html="who.btn" @click="goToVideo" class="collect-ending-btn"></div>
    </div>
    <!-- 牛皮紙 -->
    <div v-if="isShowPapper" @click="closePapper" class="collect-big-box">
      <div class="collect-papper-frame">
        <div>博物館監控到市區的怪力都消失了，看來那４隻通緝中的怪奇，你全抓到了吧。現在你可以找個舒適的地方坐一下，我會親自去回收這些怪奇，大概１０分鐘後到。你表現得很好，也許我可以考慮將你升為副館長了。<br><br>館長留</div>
        <div @click="goToEndOne" style="margin: 20px 0px 0px;" class="collect-btn">緝捕歸案</div>
      </div>
    </div>
    <!-- ling -->
    <div v-if="isShowBigLing" @click="closeBigLing" class="collect-big-box">
      <div class="collect-big-frame">
        <img v-if="lingCardType === 'color'" @click="showBigLing" class='collect-big-ling-icon' src='@/assets/images/ling/ling-color-icon.png' alt='arrived'>
        <img v-else @click="showBigLing" class='collect-big-ling-icon' src='@/assets/images/ling/ling-grey-icon.png' alt='arrived'>
      </div>
      <div class="collect-big-text" style="text-align: center;">【林開郡洋樓】</div>
      <div class="collect-big-text">分類：人造類－建築型怪奇，原形是日治時期的煤礦業鉅子所建的洋樓，後來因為產權的關係荒廢，鬼屋的傳言不脛而走。<br>洋樓被輕視、誤解的怨念，因此凝聚成憂鬱懷舊的怪奇。
</div>
    </div>
    <!-- lulu -->
    <div v-if="isShowBigLulu" @click="closeBigLulu" class="collect-big-box">
      <div class="collect-big-frame">
        <img v-if="luluCardType === 'color'" @click="showBigLulu" class='collect-big-ling-icon' src='@/assets/images/lulu/lulu-color-icon.png' alt='arrived'>
        <img v-else @click="showBigLulu" class='collect-big-ling-icon' src='@/assets/images/lulu/lulu-grey-icon-frame.png' alt='arrived'>
      </div>
      <div class="collect-big-text" style="text-align: center;">【露露】</div>
      <div class="collect-big-text">分類：人造類－物品型怪奇，原形是中華民國戒嚴時期，由船員從外國走私至委託行販賣的商品。<br>因聚集了許多賣不出去的商品的怨念，且吸收了當時常到店裡拐潘仔的舞女形象，幻化而成暴躁易怒的怪奇。</div>
    </div>
    <!-- river -->
    <div v-if="isShowBigRiver" @click="closeBigRiver" class="collect-big-box">
      <div class="collect-big-frame">
        <img v-if="riverCardType === 'color'" @click="showBigRiver" class='collect-big-ling-icon' src='@/assets/images/river/river-color-frame.png' alt='arrived'>
        <img v-else @click="showBigRiver" class='collect-big-ling-icon' src='@/assets/images/river/river-grey-frame.png' alt='arrived'>
      </div>
      <div class="collect-big-text" style="text-align: center;">【旭川河】</div>
      <div class="collect-big-text">分類：自然類－河川型怪奇，原形是日治時期挖掘而成的運河，在中華民國時期，被加蓋了明德、親民、至善三棟大樓，從此不見天日。<br>人類恣意排放、丟棄的廢棄物，逐漸與河融合而成喪失自我，不知自己存在價值的怪奇。 </div>
    </div>
    <!-- kim -->
    <div v-if="isShowBigKim" @click="closeBigKim" class="collect-big-box">
      <div class="collect-big-frame">
        <img v-if="kimCardType === 'color'" @click="showBigKim" class='collect-big-ling-icon' src='@/assets/images/commute/commute-color-frame.jpg' alt='arrived'>
        <img v-else @click="showBigKim" class='collect-big-ling-icon' src='@/assets/images/commute/commute-grey-frame.jpg' alt='arrived'>
      </div>
      <div class="collect-big-text" style="text-align: center;">【阿勤】</div>
      <div class="collect-big-text">分類：現象類－意念型怪奇，原形是基隆地區每日上萬通勤族在一成不變卻又不得不為的通勤日常裡產生的空虛感。<br>因為匯集的能量眾多，甚至獲得了扭曲時空的能力。但因為感覺太虛無了，是個做什麼都沒幹勁的傢伙。</div>
    </div>
  </div>
</template>

<script>

import cityBg from '@/assets/images/album-base.png'
export default {
  name: 'MonsterCollect',
  components: {

  },
  data () {
    return {
      getThree: false,
      getAll: false,
      cityBg: cityBg,
      // ling
      isShowBigLing: false,
      // lulu
      isShowBigLulu: false,
      // kim
      isShowBigKim: false,
      // river
      isShowBigRiver: false,
      // papper
      isShowPapper: false,
      // 最後結局
      ending: {
        man: {
          btn: '怪奇們的牢籠',
          link: 'https://youtu.be/UwiMcAVkdx4'
        },
        gg: {
          btn: '怪奇自由了',
          link: 'https://youtu.be/2EIPxeaxEcU'
        },
        no: {
          btn: '怪奇與人類共處',
          link: 'https://youtu.be/daYJgJJcPIA'
        }
      },
      who: ''
    }
  },
  mounted () {
    // 提示抓到三隻怪奇的文字
    if (this.isLingWin && this.isLuluWin && this.isKimWin) {
      this.getThree = true
    } else if (this.isRiverWin && this.isLuluWin && this.isKimWin) {
      this.getThree = true
    } else if (this.isLingWin && this.isRiverWin && this.isKimWin) {
      this.getThree = true
    } else if (this.isLingWin && this.isLuluWin && this.isRiverWin) {
      this.getThree = true
    }
    if (this.isLingWin && this.isLuluWin && this.isKimWin && this.isRiverWin) {
      this.getThree = false
      this.getAll = true
    }
    // 判斷誰贏
    if (this.whoWin === 'man') {
      this.who = this.ending.man
    } else if (this.whoWin === 'gg') {
      this.who = this.ending.gg
    } else if (this.whoWin === 'no') {
      this.who = this.ending.no
    }
  },
  methods: {
    showBigLing () {
      this.isShowBigLing = true
      this.$store.commit('switchLingArrow', false)
    },
    closeBigLing () {
      this.isShowBigLing = false
    },
    // lulu
    showBigLulu () {
      this.isShowBigLulu = true
      this.$store.commit('switchLuluArrow', false)
    },
    closeBigLulu () {
      this.isShowBigLulu = false
    },
    // river
    showBigRiver () {
      this.isShowBigRiver = true
      this.$store.commit('switchRiverArrow', false)
    },
    closeBigRiver () {
      this.isShowBigRiver = false
    },
    // kim
    showBigKim () {
      this.isShowBigKim = true
      this.$store.commit('switchKimArrow', false)
    },
    closeBigKim () {
      this.isShowBigKim = false
    },
    // papper
    openPapper () {
      this.isShowPapper = true
    },
    closePapper () {
      this.isShowPapper = false
    },
    goToEndOne () {
      this.$router.push({
        path: './end-fight-one'
      })
    },
    goToVideo () {
      window.open(this.who.link)
    }
  },
  computed: {
    lingCardType () {
      return this.$store.state.lingCardType
    },
    isLingWin () {
      return this.$store.state.isLingWin
    },
    isShowLingArrow () {
      return this.$store.state.isShowLingArrow
    },
    // kim
    kimCardType () {
      return this.$store.state.kimCardType
    },
    isKimWin () {
      return this.$store.state.isKimWin
    },
    isShowKimArrow () {
      return this.$store.state.isShowKimArrow
    },
    // lulu
    luluCardType () {
      return this.$store.state.luluCardType
    },
    isLuluWin () {
      return this.$store.state.isLuluWin
    },
    isShowLuluArrow () {
      return this.$store.state.isShowLuluArrow
    },
    // river
    riverCardType () {
      return this.$store.state.riverCardType
    },
    isRiverWin () {
      return this.$store.state.isRiverWin
    },
    isShowRiverArrow () {
      return this.$store.state.isShowRiverArrow
    },
    // 結局誰贏
    whoWin () {
      return this.$store.state.whoWin
    }
  }
}
</script>
<style lang="scss" scoped>

  .monster-collect {
    position: relative;
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .collect {

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-hint-get-three {
        position: absolute;
        color: #000;
        background-color: #95ff00;
        width: 100vw;
        padding: 8px;
        text-align: center;
      }

      &-box {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        margin-top: 140px;
        z-index: 1;
      }

      &-arrow-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-arrow {
        width: 20px;
        height: 20px;
        z-index: 1;
      }

      &-ling-icon {
        width: 23vw;
        height: auto;
        z-index: 1;
        margin: 4px;
      }

      &-big-box {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        display: flex;
        align-items: center;
        flex-direction: column;
        background-color: rgba(0,0,0,0.6);
        z-index: 2;
      }

      &-big-frame {
        margin-top: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &-big-ling-icon {
        width: 50vw;
        height: auto;
        z-index: 1;
      }

      &-big-text {
        font-size: 16px;
        margin-top: 4px;
        color: white;
        padding: 10px;
      }

      // 結局

      &-btn-box {
        position: absolute;
        width: 100%;
      }

      &-btn {
        font-size: 20px;
        color: white;
        background: green;
        width: 100px;
        height: 40px;
        text-align: center;
        line-height: 40px;
        border-radius: 20px;
        margin: auto;
        z-index: 10;
      }

      &-papper-frame {
        width: 90vw;
        height: 70vh;
        background-image: url('~@/assets/images/ending/ending-papper.png');
        display: flex;
        align-items: center;
        justify-content: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 45px;
        margin: 10vh 0px;
      }

      &-ending-btn {
        font-size: 20px;
        background-color: #95ff00;
        border-radius: 20px;
        border: 10px;
        color:gray;
        width: 180px;
        height: 40px;
        line-height: 40px;
        margin: 0px auto;
        text-align: center;
      }
    }
  }

</style>



